.snakebody{
        position:absolute;
        height: 20px;
        width:20px;
        background-color: #f4f7e5;
        opacity: 1;
        background-image:  linear-gradient(135deg, #09b106 25%, transparent 25%), linear-gradient(225deg, #09b106 25%, transparent 25%), linear-gradient(45deg, #09b106 25%, transparent 25%), linear-gradient(315deg, #09b106 25%, #eeff00 25%);
        background-position:  4px 0, 4px 0, 0 0, 0 0;
        background-size: 4px 4px;
        background-repeat: repeat;
        &.head{
            .eyes{
                position:relative;
                z-index: 10;
                background: transparent;
                svg{
                    position:absolute;
                    top: -55px;
                    left: -55px;
                    transform: scale(0.175);
                }
               
            }
        }
}

